import React from "react"
import styled from "styled-components"

const RecentsCardContainer = styled.div`
    display: flex;
    flex-direction: row;
    width: 100%;
`

const RecentsCardNumber = styled.h1`
    font-family: "Open Sans", sans;
    font-size: 3.5rem;
    font-weight: 700;
    color: #80cbc4;
`

const RecentsArticleTitle = styled.h5`
    font-family: "Open Sans", sans-serif;
    margin-top: 0.55rem;
    margin-left: 0.6rem;
`

const RecentsAuthorName = styled.p`
    font-family: "Open Sans", sans-serif;
    font-size: 0.9rem;
    margin-left: 0.6rem;
    margin-bottom: 0rem;
    font-weight: 700;
`

const RecentsDateAndReadingTime = styled.p`
    font-family: "Open Sans", sans-serif;
    font-size: 0.7rem;
    margin-left: 0.6rem;
`


const getDateLine = timestamp => {
    let dateArr = timestamp.split("T")[0].split("-")
    let monthList = [
        "Jan",
        "Feb",
        "March",
        "April",
        "May",
        "June",
        "July",
        "Aug",
        "Sept",
        "Oct",
        "Nov",
        "Dec",
    ]

    let month = monthList[parseInt(dateArr[1]) - 1]

    let finalDate = `${dateArr[2]} ${month} ${dateArr[0]}`
    return finalDate
}


const RecentsCard = ({ index, title, author, timestamp, blog_id }) => {
    return (
        <RecentsCardContainer>
            <RecentsCardNumber>{index}</RecentsCardNumber>

            <a
                href={`/article?id=${blog_id}`}
                style={{ color: "black", textDecoration: "none" }}
            >
                <div>
                    <RecentsArticleTitle>{title}</RecentsArticleTitle>

                    <RecentsAuthorName>{author}</RecentsAuthorName>

                    <RecentsDateAndReadingTime>
                        {getDateLine(timestamp)}
                    </RecentsDateAndReadingTime>
                </div>
            </a>
        </RecentsCardContainer>
    )
}

export default RecentsCard;
