import React from "react"
import styled from "styled-components"
import { Media } from "react-bootstrap"

const CardContainer = styled.div`
	font-family: "Open Sans", sans-serif;
	margin: 0.3rem;

	.title {
		font-size: 1.3rem;
	}
	.author {
		font-size: 0.8rem;
	}
	.timestamp {
		font-size: 0.6rem;
	}
`

const getDateLine = timestamp => {
    let dateArr = timestamp.split("T")[0].split("-")
    let monthList = [
        "Jan",
        "Feb",
        "March",
        "April",
        "May",
        "June",
        "July",
        "Aug",
        "Sept",
        "Oct",
        "Nov",
        "Dec",
    ]

    let month = monthList[parseInt(dateArr[1]) - 1]

    return `${dateArr[2]} ${month} ${dateArr[0]}`
}

const EPSmallCard = ({ title, author, timestamp, id, header_img }) => {
    return (
        <div>
			<a href={`/article?id=${id}`} style={{color: 'black', textDecoration: 'none'}}>
				<CardContainer>
					<Media>
						<img
							width={100}
							height={100}
							className="mr-3"
							src={header_img}
							alt="Header Image"
						/>
						<Media.Body>
							<h5 className="title">
								{title}
							</h5>
							<h6 className="author">
								<b>{author}</b>
							</h6>
							<p className="timestamp">{getDateLine(timestamp)}</p>
                            <br/>
						</Media.Body>
					</Media>
				</CardContainer>
			</a>
        </div>
    )
}

export default EPSmallCard
