import React from "react"
import styled from "styled-components"
import { Link } from "gatsby"

import SectionCard from "./SectionCard"

const SectionCardContainer = styled.div`
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-auto-rows: auto;
	grid-gap: 1rem;
	padding: 1.6rem 3rem 1rem 3rem;

	@media screen and (max-width: 800px) {
		grid-template-columns: repeat(1, 1fr);
		padding: 2rem;
	}
`

const SectionTitle = styled.h2`
	font-family: "Open Sans", sans-serif;
	font-weight: 700;
	padding: 0 0 0 3rem;
	margin: 0;
	
	@media screen and (max-width: 800px) {
		padding: 0 0 0 2rem;
	}
`

const LinkContainer = styled.div`
	font-family: "Open Sans", sans-serif;
	font-weight: 700;
	margin-top: 0.4rem;
	padding-left: 3rem;
	
	@media screen and (max-width: 800px) {
		padding: 0 0 0 2rem;
	}
`

const Section = props => {
	return (
		<div>
			<SectionTitle>{props.title}</SectionTitle>

			<SectionCardContainer>
				{props.data.map(item => (
					<SectionCard
						title={item.blog_headline}
						author={item.user_name}
						timestamp={item.create_timestamp}
						blog_id={item.pk_df_blog_page_id}
						header_img={item.header_img_path}
					/>
				))}
			</SectionCardContainer>

			<LinkContainer>
				<Link to={`/category?id=${props.title}`}>
					Read More ...
				</Link>
			</LinkContainer>

			<br />
			<br />
		</div>
	)
}

export default Section
