import React from "react"
import styled from "styled-components"
import { Image } from "react-bootstrap"

const CardContainer = styled.div`
	font-family: "Open Sans", sans-serif;
	width: 25rem;
	margin-right: 1.2rem;

	@media screen and (max-width: 1000px) {
		width: 100%;
	}

	.title {
		font-size: 1.4rem;
	}
	.author {
		font-size: 0.9rem;
		font-weight: 700;
	}
	.timestamp {
		font-size: 0.7rem;
	}
`

const getDateLine = timestamp => {
	let dateArr = timestamp.split("T")[0].split("-")
	let monthList = [
		"Jan",
		"Feb",
		"March",
		"April",
		"May",
		"June",
		"July",
		"Aug",
		"Sept",
		"Oct",
		"Nov",
		"Dec",
	]

	let month = monthList[parseInt(dateArr[1]) - 1]

	let finalDate = `${dateArr[2]} ${month} ${dateArr[0]}`
	return finalDate
}

const EPBigCard = ({ title, author, timestamp, id, header_img }) => {
	return (
		<div>
			<a
				href={`/article?id=${id}`}
				style={{ color: "black", textDecoration: "none" }}
			>
				<CardContainer>
					<Image src={header_img} fluid />
					<br />
					<br />
					<h5 className="title">{title}</h5>
					<h6 className="author">{author}</h6>
					<p className="timestamp">{getDateLine(timestamp)}</p>
				</CardContainer>
			</a>
		</div>
	)
}

export default EPBigCard
