import React from "react"
import { Card } from "react-bootstrap"

const getDateLine = timestamp => {
	let dateArr = timestamp.split("T")[0].split("-")
	let monthList = [
		"Jan",
		"Feb",
		"March",
		"April",
		"May",
		"June",
		"July",
		"Aug",
		"Sept",
		"Oct",
		"Nov",
		"Dec",
	]

	let month = monthList[parseInt(dateArr[1]) - 1]

	let finalDate = `${dateArr[2]} ${month} ${dateArr[0]}`
	return finalDate
}

const SectionCard = ({ title, author, timestamp, blog_id, header_img }) => {
	return (
		<div>
			<a
				href={`/article?id=${blog_id}`}
				style={{
					textDecoration: "none",
					color: "black",
					fontFamily: "Open Sans, sans-serif",
				}}
			>
				<Card style={{height: '100%'}}>
					<Card.Img variant="top" src={header_img} />
					<Card.Body>
						<Card.Title style={{ fontWeight: 700 }}>
							{title}
						</Card.Title>
						<Card.Text>
							{author} <br /> {getDateLine(timestamp)}
						</Card.Text>
					</Card.Body>
				</Card>
			</a>
		</div>
	)
}

export default SectionCard
