import React, { useState, useEffect } from "react"
import Loader from "react-loader-spinner"
import styled from "styled-components"

import "bootstrap/dist/css/bootstrap.min.css"

import SEO from "../components/seo"
import NavigationBar from "../components/NavigationBar"
import EditorsPicks from "../components/EditorsPicks/EditorsPicks"
import Recents from "../components/Recents/Recents"
import Section from "../components/Section/Section"
import HomeCarousel from "../components/Carousel/HomeCarousel"
import Footer from "../components/Footer"

import backend_url from "../components/urls";

const EPAndRecentsContainer = styled.div`
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-evenly;
	padding: 2rem;

	@media screen and (max-width: 1000px){
		justify-content: flex-start;
	}
`
const SectionContainer = styled.div`
	display: flex;
	justify-content: center;

`

const IndexPage = () => {
	const [articleData, setArticleData] = useState({})
	const [loading, setLoading] = useState(true)

	const getArticleData = async () => {
		const response = await fetch(`${backend_url}/api/blog/`)
		const data = await response.json()

		setArticleData(data)
		setLoading(false)
	}

	const getSectionTitle = (section) => {
		let sectionMap = new Map();
		sectionMap.set('education', 'Education and RTE');
		sectionMap.set('iDesire', 'iDesire');
		sectionMap.set('adhyayan', 'Adhyayan');
		sectionMap.set('my_journey', 'My Journey');
		sectionMap.set('news', 'NEWS');
		sectionMap.set('festivals', 'Festivals');
		sectionMap.set('untold_stories', 'Untold Stories');

		return sectionMap.has(section) ? sectionMap.get(section) : 'None';
	}

	useEffect(() => {
		getArticleData()
	}, [loading])


	return (
		<div>
			<SEO title="Blog - Desire Foundation"/>
			<NavigationBar/>

			<br/>
			<br/>

			{loading ? (
				<div style={{ textAlign: "center", padding: "2rem" }}>
					<Loader
						type="Puff"
						color="#00BFFF"
						height={50}
						width={50}
					/>
				</div>
			) : (
				<div>
					<HomeCarousel data={articleData['carousel']}/>

					<EPAndRecentsContainer>
						<EditorsPicks data={articleData['editorsPick']}/>
						<Recents data={articleData['recents']}/>
					</EPAndRecentsContainer>

					{Object.keys(articleData['sections']).map((section_name) => {
						return (
							<SectionContainer>
								{articleData['sections'][section_name].length !== 0 ? (
									<Section title={getSectionTitle(section_name)} data={articleData['sections'][section_name]}/>
								): (<div/>)}
							</SectionContainer>
						)
					})}

					<Footer/>
				</div>
			)}
		</div>
	)
}

export default IndexPage

