import React from "react"
import styled from "styled-components"

const CContainer = styled.div`
	display: flex;
	justify-content: center;
`

const ImageContainer = styled.div`
	width: 50%;
	height: auto;
	background-image: url(${props => props.path});
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;

	@media screen and (max-width: 800px) {
		width: 70%;
	}
`

const TextContainer = styled.div`
	font-family: "Open Sans", sans-serif;
	width: 50%;
	height: 20rem;
	background-color: #dcdcdc;
	padding: 2rem;

	h1 {
		font-size: 1.7rem;
	}

	h2 {
		font-size: 1.3rem;
		font-weight: bold;
	}

	p {
		font-size: 1.2rem;
	}

	h5 {
		font-weight: bold;
	}

	@media screen and (max-width: 1000px) {
		padding: 1rem;
		height: 15rem;

		h1 {
			font-size: 1.2rem;
		}

		h2 {
			font-size: 1rem;
			font-weight: bold;
		}

		p {
			font-size: 1rem;
		}

		h5 {
			font-size: 0.9rem;
			font-weight: bold;
		}
		h6 {
			font-size: 0.8rem;
		}
	}
`

const Title = styled.div`
	margin-top: 3rem;
	
	@media screen and (max-width: 800px){
		margin-top: 1.5rem;
	}
`

const Author = styled.div`
	@media screen and (min-width: 1000px) {
		position: absolute;
		bottom: 10%;
	}
`

const getDateLine = timestamp => {
	let dateArr = timestamp.split("T")[0].split("-")
	let monthList = [
		"Jan",
		"Feb",
		"March",
		"April",
		"May",
		"June",
		"July",
		"Aug",
		"Sept",
		"Oct",
		"Nov",
		"Dec",
	]

	let month = monthList[parseInt(dateArr[1]) - 1]

	return `${dateArr[2]} ${month} ${dateArr[0]}`
}

const CarouselContent = ({
	title,
	sub_title,
	author,
	timestamp,
	blog_id,
	img_path,
}) => {
	return (
		<div>
			<a
				href={`/article?id=${blog_id}`}
				style={{ color: "black", textDecoration: "none" }}
			>
				<CContainer>
					<ImageContainer path={img_path} />
					<TextContainer>
						<h1>FEATURED</h1>
						<Title>
							<h2>{title}</h2>
							<p>{sub_title}</p>
						</Title>
						<Author>
							<h5>{author}</h5>
							<h6>{getDateLine(timestamp)}</h6>
						</Author>
					</TextContainer>
				</CContainer>
			</a>
		</div>
	)
}

export default CarouselContent
